$my-general-color: rgb(162, 59, 59);

@keyframes colorAnimation{
    0% {border-color: white;}
    100% {border-color:$my-general-color;}
}
.pageContent{
    width: auto;
    margin: auto 3%;    
   
}
.infoCard{
    display: flex;    
    flex-wrap: wrap;
    margin-top: 10px;
   

}
.cardImage{
    width: 50%;
    text-align: center ;
    background-color: $my-general-color;
    color: white;
    h3{
        font-style: italic;
        color: white;
       
    }
    img{
        height: 100px;
        width: 100px;
        border-radius: 50%;
    }
}
.cardDesc{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    width: 50%;    
    background-color: whitesmoke; 
    p{        
        text-align: justify;
    }   
}
.selectCard{
    margin-top: 15px;
    width: 100%;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;   
    gap: 10px;    
   
    img{
        
        flex-basis: 350px;      
        height: 100%;
        width: 100%;
        height: 400px;
        object-fit: cover;
    }
}
.selectTitle{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    button{
        margin: 1% 2%;
        border: none;
        font-size: 100%;
        background: none;
        padding: 2%;
       
        transition: all 0.5s ease-in-out;
        border-bottom: 5px solid;
        animation-name: colorAnimation;   
        animation-duration: 3s;
        animation-iteration-count: infinite;
        animation-direction: alternate;
       
        &:hover{
            background-color: $my-general-color;
            border-radius: 10px;
            animation-iteration-count: 0;
            color: white;

        }
    }
 
        
    
}